.header {
  height: auto;
  width: 100vw;
  padding-top: 70px;
  background-color: #f5f5f5;
}

.header img {
  width: 100%;
}

.arrow {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: auto;
  width: 100%;
  position: absolute;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-top: 70px;
}

.arrow--left {
  z-index: 1;
  margin: 40px;
  opacity: 0.8;
}

.arrow--left:hover {
  cursor: pointer;
  opacity: 1;
}

.arrow-right {
  z-index: 1;
  margin: 40px;
  opacity: 0.8;
}

.arrow-right:hover {
  cursor: pointer;
  opacity: 1;
}

.slider {
  height: auto;
  width: 100%;
  z-index: -1;
  background-color: #f5f5f5;
}

.slider img {
  height: 292px;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-box-shadow: 0 20px 25px -3px #d1d5db, 0 4px 6px -4px #d1d5db;
          box-shadow: 0 20px 25px -3px #d1d5db, 0 4px 6px -4px #d1d5db;
}

.slider h1 {
  position: absolute;
  text-align: center;
  top: 180px;
  left: 0;
  right: 0;
  font-size: 40px;
  font-weight: 400;
  padding: 0;
  margin: 0;
  color: #fafafa;
}

.caption h1 {
  font-weight: 300;
  color: #fafafa;
}
